<template>
  <div>
      <div class="title">
           设备
      </div>
      <!-- 查询框 -->
      <!-- <el-form :inline="true" :model="formInline" class="demo-form-inline search">
        <el-form-item label="通道">
          <el-select v-model="formInline.region" placeholder="通道">
            <el-option label="通道一" value="shanghai"></el-option>
            <el-option label="通道二" value="beijing"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="设备名称">
          <el-input v-model="formInline.user" placeholder="设备名称，模糊查询"></el-input>
        </el-form-item>
        <el-form-item label="序列号">
          <el-input v-model="formInline.user" placeholder="序列号、模糊查询"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">查询</el-button>
        </el-form-item>
      </el-form> -->
      <!-- 选择更新时间 -->
      <el-select v-model="value" @change='shijian' placeholder="请选择">
          <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
          </el-option>
      </el-select>
      <!-- 列表 -->
      <div class="tabBox">
        <el-table
          :data="tableData"
          stripe
          style="width: 100%">
          <el-table-column
            prop="id"
            label="设备id"
            width="180">
          </el-table-column>
          <el-table-column
            prop="name"
            label="设备名称">
          </el-table-column>
          <el-table-column
            label="最近修改的时间">
              <template slot-scope="scope">
                  {{scope.row.nodeVersion | timestamp}}
              </template>
          </el-table-column>
          <el-table-column
            width="180"
            label="操作">
              <template slot-scope="scope">
                <el-dropdown>
                  <el-button type="primary">
                    操作<i class="el-icon-arrow-down el-icon--right"></i>
                  </el-button>
                  <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item @click.native="FnMFF(scope.row.id)">组态图</el-dropdown-item>
                    <el-dropdown-item @click.native="FnMFMechanics(scope.row.id)">机械数据</el-dropdown-item>
                  </el-dropdown-menu>
                </el-dropdown>
              </template>
          </el-table-column>
        </el-table>

      </div>
  </div>
</template>
<script>
import url from "../../common/api";
import { mapState } from "vuex";
  export default {
    data() {
      return {
        formInline: {//查询的的提交数据
          user: '',
          region: ''
        },
        tableData: [
          {//这是表单的数据
          date: '参数',
          name: '参数22',
          address: '参数三 1518 弄'
          }, {
          date: '2016-05-04',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1517 弄'
          }, {
          date: '2016-05-01',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1519 弄'
          }, {
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1516 弄'
          }
        ],
        chesi:false,//这是下来菜单
        dialogTableVisible:false,//关联用户显示/隐藏的变量
        filterText: '',//关联用户搜素狂输入
        data: [{//作者是等级列表
            id: 1,
            label: '一级 1',
            children: [{
              id: 4,
              label: '二级 1-1',
              children: [{
                id: 9,
                label: '三级 1-1-1'
              }, {
                id: 10,
                label: '三级 1-1-2'
              }]
            }]
          }, {
            id: 2,
            label: '一级 2',
            children: [{
              id: 5,
              label: '二级 2-1'
            }, {
              id: 6,
              label: '二级 2-2'
            }]
          }, {
            id: 3,
            label: '一级 3',
            children: [{
              id: 7,
              label: '二级 3-1'
            }, {
              id: 8,
              label: '二级 3-2'
            }]
        }],
        defaultProps: {//这是几级定义
          children: 'children',
          label: 'label'
        },
        data: [],
        tableD:[],//这是绑定的人员数据表
        tableDatareRE:[],
        options: [{
            value: 300000,
            label: '五分钟'
            }, {
            value: 60000,
            label: '一分钟'
            }, {
            value: 30000,
            label: '三十秒'
            }, {
            value: 10000,
            label: '十秒'
        }],
        value: 60000,
        timer:'',
      };
    },
    methods: {
      onSubmit() {//点击查询表单的按钮触发的案件
        console.log('submit!');
      },
      filterNode(value, data) {//这是关联用户筛选出符合条件的数据
        if (!value) return true;
        return data.label.indexOf(value) !== -1;
      },
      FntaiZang(i){//这是操作-跳转到台账  i参数是代表那一列的数据
        this.$router.push('/index/MFtaiZang')
      },
      FnMFF(i){//这是操作-跳转到组态图  i参数是代表那一列的数据
        this.$router.push({
          path:'/index/MFconfiguration', 
          query:{
          id:i
          }
        })
      },
      FnMFMechanics(i){//这是操作-机械设备  i--id
        this.$router.push({
          path:'/index/MFMechanics', 
          query:{
          id:i
          }
        })
      },
      FnMFval(i){//这是操作-跳转到常用数据  i参数是代表那一列的数据
        this.$router.push('/index/MFval')
      },
      FnnodeIds(){//采集设备研华+后台请求
      url.collectdeviceattlistv3({
        user_id:this.userInfo.user_id,
        company_id:this.userInfo.u_company_id
      }).then(res=>{
        console.log(res,'--------------x')
        url.nodeIds(res.data.url[0])
        .then(res=>{
          console.log(res,'-------------研华')
          this.tableData=res.data
        })
      })
        this.FnInterval()
      },
      FnInterval(){
        console.log(this.value,'----------------时间')
        this.timer=setInterval(()=>{
            url.collectdeviceattlistv3({
              user_id:this.userInfo.user_id,
              company_id:this.userInfo.u_company_id
            }).then(res=>{
              console.log(res,'--------------x')
              url.nodeIds(res.data.url[0])
              .then(res=>{
                console.log(res,'-------------研华')
                this.tableData=res.data
              })
            })
        },this.value)

      },
      shijian(a,b,c){
          console.log(a,b,c,this.value,'------------离开')
          clearInterval(this.timer)
          this.FnInterval()
      }
    },
    computed:{
      ...mapState(['userInfo'])
    },
    mounted(){
      this.FnnodeIds()//开始
    },
    beforeRouteLeave(to,from,next){
      clearInterval(this.timer)
      next()
    }
  }
</script>
<style lang='stylus' scoped>
.title
    font-size 20px
    color #333
    margin-bottom 20px
    margin-top 20px
    padding-left 15px
    border-left 3px solid #409EFF
.search
  margin-left 20px
.tabBox
  padding 30px
  min-width 920px
  box-sizing border-box
.relationBox_b
  width 1800px
  .jgTab
    display flex
    margin 30px
    .jieGou
      width 300px
    .jieGou_tit
      margin-bottom 17px
    .tablebox
      flex 1
      margin-left 10px
</style>


